/**
 * @file    css reset file
 * @refer   https://github.com/firminoweb/csshorus/blob/master/css/horus/reset.css
 * @author  liulifei
 * @data    2016-11-04
 **/
html,
body {
  width: 100%;
  min-height: 100%;
  font: 0.24rem/1.5 Microsoft YaHei, SimHei, SimSun, Arial, Verdana;
}
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
address,
img,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: normal;
}
body {
  /* 禁止设备弹出列表按钮 */
  -webkit-touch-callout: none;
  /* 禁止选择文本 */
  /*-webkit-user-select: none;*/
  /* 避免屏幕旋转导致字体大小缩放 */
  -webkit-text-size-adjust: 100%;
  /* 禁止IOS系统上点击触发反馈 */
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
/*  ===typography=== */
a {
  text-decoration: none;
  outline: 0;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote::before,
blockquote::after,
q::before,
q::after {
  content: none;
}
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}
fieldset,
img {
  border: none;
}
em,
i {
  font-style: normal;
}
/*  ===form=== */
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  vertical-align: middle;
  -webkit-user-select: auto;
  padding: 0;
  outline: medium;
}
textarea {
  resize: none;
  overflow: auto;
  vertical-align: top;
}
[hidden] {
  display: none;
}
/*  ===table=== */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* ios下的safari，会出现莫名其妙的阴影，圆角，所以要重置下 */
input,
textarea {
  -webkit-appearance: none;
  border-radius: 0;
}
/*retina上模拟1px, 2px...*/
/**清除浮动**/
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clearfix {
  display: inline-block;
}
* html .clearfix {
  height: 1%;
}
.clearfix {
  display: block;
}
.clear {
  clear: both;
  height: 0;
  font: 0/0 Arial;
  visibility: hidden;
  line-height: 0;
}
